// lambda/closet/admin.ts

import {
  DynamoDBClient,
  QueryCommand,
  UpdateItemCommand,
  GetItemCommand,
} from "@aws-sdk/client-dynamodb";
import {
  SFNClient,
  SendTaskFailureCommand,
  SendTaskSuccessCommand,
} from "@aws-sdk/client-sfn";

const ddb = new DynamoDBClient({});
const sfn = new SFNClient({});

const { TABLE_NAME = "", APPROVAL_SM_ARN = "" } = process.env;
if (!TABLE_NAME) {
  throw new Error("Missing env: TABLE_NAME");
}

const nowIso = () => new Date().toISOString();
const S = (v: string) => ({ S: v });

type AppSyncEvent = {
  info: { fieldName: string };
  arguments: any;
  identity?: any;
};

type ClosetStatus = "DRAFT" | "PENDING" | "APPROVED" | "REJECTED" | "PUBLISHED";

type ClosetItem = {
  id: string;
  userId: string;         // <- what the frontend expects
  ownerSub: string;       // same value as userId, just explicit
  status: ClosetStatus;
  createdAt: string;
  updatedAt: string;
  mediaKey: string;
  title: string;
  reason?: string;
};

function getIdentity(event: AppSyncEvent) {
  const claims = (event.identity as any)?.claims || {};
  const sub = (event.identity as any)?.sub || claims.sub;
  const groupsRaw = claims["cognito:groups"];
  const groups = Array.isArray(groupsRaw)
    ? groupsRaw
    : String(groupsRaw || "")
        .split(",")
        .map((x) => x.trim())
        .filter(Boolean);

  const isAdmin = groups.includes("ADMIN") || groups.includes("COLLAB");

  if (!sub) {
    throw new Error("Unauthorized");
  }

  return { sub, isAdmin };
}

function mapItem(raw: any): ClosetItem {
  return {
    id: raw.id.S!,
    userId: raw.ownerSub.S!,   // map to userId for GraphQL type
    ownerSub: raw.ownerSub.S!,
    status: raw.status.S! as ClosetStatus,
    createdAt: raw.createdAt.S!,
    updatedAt: raw.updatedAt.S!,
    mediaKey: raw.mediaKey?.S ?? "",
    title: raw.title?.S ?? "",
    reason: raw.reason?.S ?? "",
  };
}

/* ===================== Queries ===================== */

/** Admin queue – pending items */
export const adminListPending = async (event: AppSyncEvent): Promise<ClosetItem[]> => {
  const { isAdmin } = getIdentity(event);
  if (!isAdmin) {
    throw new Error("Forbidden");
  }

  const out = await ddb.send(
    new QueryCommand({
      TableName: TABLE_NAME,
      IndexName: "gsi2", // status index
      KeyConditionExpression: "gsi2pk = :p",
      ExpressionAttributeValues: { ":p": S("STATUS#PENDING") },
      ScanIndexForward: true,
      ProjectionExpression:
        "id, ownerSub, #s, createdAt, updatedAt, mediaKey, title, reason",
      ExpressionAttributeNames: { "#s": "status" },
    }),
  );

  return (out.Items || []).map((it) =>
    mapItem({
      ...it,
      status: it["status"],
    }),
  );
};

/** Fan-facing closet feed – published looks */
export const closetFeed = async (event: AppSyncEvent): Promise<ClosetItem[]> => {
  // Any signed-in user can see the feed; still parse identity for sub
  getIdentity(event); // will throw if not authed

  const { sort = "NEWEST" } = event.arguments || {};

  const out = await ddb.send(
    new QueryCommand({
      TableName: TABLE_NAME,
      IndexName: "gsi2",
      KeyConditionExpression: "gsi2pk = :p",
      ExpressionAttributeValues: { ":p": S("STATUS#PUBLISHED") },
      ScanIndexForward: sort === "NEWEST", // newest first if true
      ProjectionExpression:
        "id, ownerSub, #s, createdAt, updatedAt, mediaKey, title",
      ExpressionAttributeNames: { "#s": "status" },
    }),
  );

  const items = (out.Items || []).map((it) =>
    mapItem({
      ...it,
      status: it["status"],
    }),
  );

  if (sort === "MOST_LOVED") {
    // If you have a loveCount attribute, you can sort here.
    // For now, keep it simple and just reuse the same list.
    return items;
  }

  return items;
};

/** Simple alias used for admin "Top looks" tab – same as closetFeed for now */
export const topClosetLooks = async (event: AppSyncEvent): Promise<ClosetItem[]> => {
  return closetFeed({ ...event, arguments: { sort: "NEWEST" } });
};

/* ===================== Mutations ===================== */

/** Approve an item */
export const adminApproveItem = async (event: AppSyncEvent): Promise<ClosetItem> => {
  const { isAdmin } = getIdentity(event);
  if (!isAdmin) throw new Error("Forbidden");

  const id = event.arguments?.id as string;
  if (!id) throw new Error("id required");

  const now = nowIso();

  const out = await ddb.send(
    new UpdateItemCommand({
      TableName: TABLE_NAME,
      Key: { pk: S(`ITEM#${id}`), sk: S("META") },
      UpdateExpression:
        "SET #s = :s, updatedAt = :u, gsi2pk = :g2pk, gsi2sk = :g2sk REMOVE reason, approvalToken",
      ExpressionAttributeNames: { "#s": "status" },
      ExpressionAttributeValues: {
        ":s": S("APPROVED"),
        ":u": S(now),
        ":g2pk": S("STATUS#APPROVED"),
        ":g2sk": S(now),
      },
      ReturnValues: "ALL_OLD",
    }),
  );

  const prev = out.Attributes!;
  const token = prev.approvalToken?.S;

  if (token) {
    // If the approval workflow was waiting, unblock it
    try {
      await sfn.send(
        new SendTaskSuccessCommand({
          taskToken: token,
          output: JSON.stringify({ approved: true }),
        }),
      );
    } catch {
      // best effort
    }
  }

  return mapItem({
    ...prev,
    status: S("APPROVED"),
  });
};

/** Reject an item */
export const adminRejectItem = async (event: AppSyncEvent): Promise<ClosetItem> => {
  const { isAdmin } = getIdentity(event);
  if (!isAdmin) throw new Error("Forbidden");

  const id = event.arguments?.id as string;
  const reason = (event.arguments?.reason as string) || "";
  if (!id) throw new Error("id required");

  const now = nowIso();

  const out = await ddb.send(
    new UpdateItemCommand({
      TableName: TABLE_NAME,
      Key: { pk: S(`ITEM#${id}`), sk: S("META") },
      UpdateExpression:
        "SET #s = :s, updatedAt = :u, gsi2pk = :g2pk, gsi2sk = :g2sk, reason = :r REMOVE approvalToken",
      ExpressionAttributeNames: { "#s": "status" },
      ExpressionAttributeValues: {
        ":s": S("REJECTED"),
        ":u": S(now),
        ":g2pk": S("STATUS#REJECTED"),
        ":g2sk": S(now),
        ":r": S(reason),
      },
      ReturnValues: "ALL_OLD",
    }),
  );

  const prev = out.Attributes!;
  const token = prev.approvalToken?.S;

  if (token) {
    try {
      await sfn.send(
        new SendTaskFailureCommand({
          taskToken: token,
          error: "Rejected",
          cause: reason || "Rejected by admin",
        }),
      );
    } catch {
      // best effort
    }
  }

  return mapItem({
    ...prev,
    status: S("REJECTED"),
    reason: S(reason),
  });
};

/** Placeholder for changing audience; safe no-op for now */
export const adminSetClosetAudience = async (_event: AppSyncEvent): Promise<string> => {
  // You can wire this later; returning a string keeps the schema happy.
  return "NOT_IMPLEMENTED";
};

/* ===================== Dispatcher (if this Lambda is used directly) ===================== */

export const handler = async (event: AppSyncEvent) => {
  const f = event.info.fieldName;

  if (f === "adminListPending") return adminListPending(event);
  if (f === "adminApproveItem") return adminApproveItem(event);
  if (f === "adminRejectItem") return adminRejectItem(event);
  if (f === "adminSetClosetAudience") return adminSetClosetAudience(event);
  if (f === "closetFeed") return closetFeed(event);
  if (f === "topClosetLooks") return topClosetLooks(event);

  throw new Error(`No resolver for field ${f}`);
};
